@import url(https://fonts.googleapis.com/css?family=Oswald&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

.MyNavbar {
  display: block; }

.navbar {
  box-shadow: inset 4px 4px 15px rgba(255, 255, 255, 0.1), inset -4px -4px 15px rgba(255, 255, 255, 0.1); }

.CounterRow {
  display: flex;
  width: 100%;
  flex-direction: row; }

.borderSpace {
  margin-right: 15px;
  padding-right: 15px; }

.countersRow {
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  padding-top: 5px;
  padding-bottom: 5px;
  box-shadow: inset 4px 4px 15px rgba(255, 255, 255, 0.1), inset -4px -4px 15px rgba(255, 255, 255, 0.1); }

.counterCard {
  box-shadow: none; }

.counterMiddleBorder {
  width: 15px;
  padding: 0 15px;
  margin: 0 15px; }

.hardCounter {
  border: 2px solid #e44747; }

.hardCountersRow {
  display: flex;
  flex-direction: row; }

.softCounter {
  border: 2px solid #9B47E4; }

.softCountersRow {
  display: flex;
  flex-direction: row; }

.opponentImg {
  border: 2px solid #9B47E4; }

.teamName {
  font-weight: 400;
  font-size: .8rem;
  margin: 0; }

.toonImg {
  width: 35px;
  height: 35px;
  border-radius: 50%;
  margin: 0 2.5px; }

@media only screen and (min-width: 768px) {
  /* for desktop */
  .toonImg {
    width: 45px;
    height: 45px; } }

@media only screen and (min-width: 992px) {
  /* for desktop */
  .toonImg {
    width: 55px;
    height: 55px; } }

@media only screen and (min-width: 1200px) {
  /* for desktop */
  .toonImg {
    width: 65px;
    height: 65px; } }

.Counters5v5 {
  display: flex;
  flex-direction: column;
  text-align: center; }

.columnTitles {
  display: flex;
  flex-direction: row;
  margin-top: 10px; }
  .columnTitles h1 {
    font-size: 1.7rem; }

.columnTeams {
  display: flex;
  flex-direction: column; }

@media only screen and (min-width: 768px) {
  /* for desktop */
  .columnTitles h1 {
    font-size: 2rem; } }

.App {
  display: block;
  background-color: black;
  box-shadow: inset 0px 0px 10px rgba(255, 255, 255, 0.1), inset 0px 0px -10px rgba(255, 255, 255, 0.1);
  font-family: 'Oswald', sans-serif;
  color: whitesmoke; }

